import PageHeader from './PageHeader.vue'
import interfaces from '../../../utils/interface.js'
import Swiper from '../../../components/Swiper.vue'

export default {
	components: {
		PageHeader,
		Swiper
	},
	data() {
		return {
			swiper_list: [], //轮播图片
			category_list: [] ,//分类菜单
			promotion: [] ,//活动
			touch: false
		}
	},
	methods: {
		init_data() {
			this.$request({
			    url: interfaces.getMallData,
			    success: ((res) => { //如果使用对象的形式传递方法就这样使用 --- 回调函数
				  this.swiper_list = res.data.swiperList;
				  this.category_list = res.data.categoryList;
				  this.promotion = res.data.promotion;
				})
			});
		},
		touch_event(delay,touch) {
			setTimeout(() => {
				this.touch = touch;
			},delay);
		},
		handleCategory(item) { //分类跳转
			uni.navigateTo({ //打开页面
				url: '../../goods/goodsList?name='+item.name
			})
		},
		handlePromotion(item) { //优惠专区
			uni.showToast({
				title: item.title,
				icon: 'none'
			});
		}
	},
	onLoad() { //页面加载完毕时
		this.init_data();
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
	}
}
